import React, { InputHTMLAttributes } from 'react';
import { FieldError } from 'react-hook-form';
import { InputWrapper, StyledInput, ErrorMessage } from './InputField.styled';

const InputField: React.FC<
  {
    register: any;
    error?: FieldError;
  } & InputHTMLAttributes<HTMLInputElement>
> = ({ register, error, ...atributes }) => {
  return (
    <InputWrapper>
      <StyledInput {...register} {...atributes} />
      <ErrorMessage>{error?.message ? error.message : '\u00A0'}</ErrorMessage>
    </InputWrapper>
  );
};

export default InputField;
