import React from 'react';
import { TDom, verifyHTML } from '../../services/verifyhtml';
import { HtmlNode } from './HtmlNode';

export const HtmlComponent = React.memo(
  ({ html }: { html: string }) => {
    const dom: TDom[] = verifyHTML(html);
    return (
      <>
        {dom.map((node, index) => (
          <HtmlNode key={index} dom={node} />
        ))}
      </>
    );
  },
  (prevProps, nextProps) => prevProps.html === nextProps.html
);
