import { TDom } from '../../services/verifyhtml';
import { LinkElement } from './HtmlNode.styled';

export const HtmlNode: React.FC<{
  dom: TDom;
}> = ({ dom }) => {
  if (dom.tag === 'text') {
    return dom.text as any;
  } else if (dom.tag === 'newLine') {
    return <br />;
  } else if (dom.tag === 'a') {
    return (
      <LinkElement {...dom.attrs}>
        {dom.children?.map((child, index) => (
          <HtmlNode key={index} dom={child} />
        ))}
      </LinkElement>
    );
  } else if (dom.tag === 'code') {
    return (
      <code {...dom.attrs}>
        {dom.children?.map((child, index) => (
          <HtmlNode key={index} dom={child} />
        ))}
      </code>
    );
  } else if (dom.tag === 'i') {
    return (
      <i {...dom.attrs}>
        {dom.children?.map((child, index) => (
          <HtmlNode key={index} dom={child} />
        ))}
      </i>
    );
  } else if (dom.tag === 'strong') {
    return (
      <strong {...dom.attrs}>
        {dom.children?.map((child, index) => (
          <HtmlNode key={index} dom={child} />
        ))}
      </strong>
    );
  }
  return '' as any;
};
