import React, { useState } from 'react';
import {
  MessageContainer,
  Header,
  Body,
  MessageDate,
  MessageEmail,
  MessageHomepage,
  MessageName,
  AddCommentButton,
} from './Message.styled';
import { TOneComment } from '../../store/Comment/types';
import { useAppDispatch, useAppSelector } from '../../hooks';
import { allCommentsAction } from '../../store/Comment/actions';
import { HtmlComponent } from '../HtmlComponent/HtmlComponent';
import { MessageForm } from '../MessageForm/MessageForm';
import 'lightbox2/dist/css/lightbox.min.css';
import 'lightbox2/dist/js/lightbox-plus-jquery.min.js';
import axios from 'axios';
import { Modal } from '../Modal/Modal';

export const MessageComponent: React.FC<{
  message: TOneComment;
  level?: number;
}> = ({ message, level = 0 }) => {
  const dispatch = useAppDispatch();
  const statusForm =
    useAppSelector((s) => s.commentState.statusForms[message._id]) ?? 'closed';

  const [fileText, setFileText] = useState<string | null>(null);

  const handleCancel = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(
      allCommentsAction.changeFormStatus({
        id: message._id,
        value: statusForm === 'closed' ? 'opened' : 'closed',
      })
    );
  };

  const openFile = async () => {
    const response = await axios.get(message.file!);

    setFileText(response.data);
  };
  return (
    <>
      <MessageContainer level={level}>
        <Header>
          <MessageName>{message.name}</MessageName>
          <MessageEmail>{message.email}</MessageEmail>
          <MessageDate>{new Date(message.date).toLocaleString()}</MessageDate>
          {message.homepage && (
            <MessageHomepage
              href={message.homepage}
              target='_blank'
              rel='noopener noreferrer'
            >
              Home page
            </MessageHomepage>
          )}
        </Header>
        <Body>
          <HtmlComponent html={message.text} />
          <br />
          {message.file &&
            (message.file.match(/\.(jpeg|jpg|gif|png)/i) ? (
              <a href={message.file} data-lightbox={message._id}>
                <img src={message.file} alt='Attached content' />
              </a>
            ) : (
              // eslint-disable-next-line jsx-a11y/anchor-is-valid
              <a href='#' onClick={openFile} className='file-button'>
                Open File
              </a>
            ))}
        </Body>
        <AddCommentButton onClick={handleCancel} className='add-comment-btn'>
          {statusForm !== 'closed' ? '' : ''}
        </AddCommentButton>
      </MessageContainer>
      {statusForm !== 'closed' && (
        <MessageForm message_id={message._id} level={level} />
      )}
      {message.replies?.map((reply, index) => (
        <MessageComponent key={index} message={reply} level={level + 1} />
      ))}
      <Modal
        isOpen={fileText !== null}
        onClose={() => setFileText(null)}
        hideOnOverlayClick={true}
        closeButtonShown={false}
      >
        <HtmlComponent html={fileText!} />
      </Modal>
    </>
  );
};
