import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { TOneTopic } from '../../store/Comment/types';
import { allCommentsAction } from '../../store/Comment/actions';
import { useAppDispatch, useAppSelector } from '../../hooks';
import { HtmlComponent } from '../HtmlComponent/HtmlComponent';

//   
const Table = styled.table`
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;

  th,
  td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  td:nth-child(1) {
    width: 15%;
  }
  td:nth-child(2) {
    width: 15%;
  }
  td:nth-child(3) {
    width: 15%;
  }
  td:nth-child(4) {
    max-width: 0;
  }

  th {
    background-color: #0073e6;
    color: white;
    &:nth-child(-n + 3):hover {
      cursor: pointer;
      &:hover {
        background-color: #005bb5;
      }
    }
  }

  tr:nth-child(even) {
    background-color: #f9f9f9;
  }

  tr:hover {
    background-color: #f1f1f1;
  }
`;

export const TopicTable: React.FC = () => {
  const dispatch = useAppDispatch();
  const { topicsList } = useAppSelector((state) => state.commentState);
  const [sortedMessages, setSortedMessages] = useState<TOneTopic[]>([]);
  const [sortField, setSortField] = useState<keyof TOneTopic>('date');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc');

  const sort = () => {
    console.log('sort', topicsList, sortField, sortOrder);
    const sorted = [...topicsList].sort((a, b) => {
      let res = 0;
      if (typeof a[sortField] === 'string') {
        res = ((a[sortField] as string) || '').localeCompare(
          (b[sortField] as string) || ''
        );
      } else {
        res = (+a[sortField] || 0) - (+b[sortField] || 0);
      }
      return { asc: res, desc: -res }[sortOrder];
    });

    setSortedMessages(sorted);
  };

  useEffect(() => {
    dispatch(allCommentsAction.getTopicsAction());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    sort();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [topicsList, sortField, sortOrder]);

  const handleSort = (field: keyof TOneTopic) => {
    const isAscending = sortField === field && sortOrder === 'asc';
    const newSortOrder = isAscending ? 'desc' : 'asc';

    setSortField(field);
    setSortOrder(newSortOrder);
  };

  const changeTopic = (id: string) => {
    dispatch(allCommentsAction.changeTopic(id));
  };

  return (
    <Table>
      <thead>
        <tr>
          <th onClick={() => handleSort('name')}>User Name</th>
          <th onClick={() => handleSort('email')}>E-mail</th>
          <th onClick={() => handleSort('date')}>Date</th>
          <th>Text</th>
        </tr>
      </thead>
      <tbody>
        {sortedMessages.map((message, index) => (
          <tr key={index} onClick={() => changeTopic(message._id)}>
            <td>{message.name}</td>
            <td>{message.email}</td>
            <td>{new Date(message.date).toLocaleString()}</td>
            <td>
              <HtmlComponent html={message.text.replaceAll('\n', ' ')} />
            </td>
          </tr>
        ))}
      </tbody>
    </Table>
  );
};
