import React, { useEffect, useState } from 'react';
import { Overlay, ModalContent, CloseButton } from './Modal.styled';

export const Modal: React.FC<{
  isOpen: boolean;
  onClose: () => void;
  children: React.ReactNode;
  hideOnOverlayClick?: boolean;
  closeButtonShown?: boolean;
}> = ({ isOpen, onClose, children, hideOnOverlayClick = false,closeButtonShown=true }) => {
  const [show, setShow] = useState(false);
  const [isOpened, setIsOpened] = useState(isOpen);
  
  useEffect(() => {
    if (isOpened) {
      setShow(true);
    } else {
      setTimeout(() => {
        setShow(false);
        onClose();
      }, 250);
    }
  }, [isOpened]);

  useEffect(() => {
    setIsOpened(isOpen);
  }, [isOpen]);

  const closeModal = () => {
    setIsOpened(false);
  }

  return (
    <Overlay
      isOpen={show || isOpened}
      onClick={hideOnOverlayClick ? closeModal : undefined}
    >
      <ModalContent
        isOpen={show && isOpened}
        onClick={(e) => e.stopPropagation()}
      >
        {closeButtonShown&&<CloseButton onClick={closeModal}>&times;</CloseButton>}
        {(show || isOpened) && children}
      </ModalContent>
    </Overlay>
  );
};
