import React, { useState, useEffect } from 'react';
import { api } from '../../services';
import {
  CaptchaBody,
  CaptchaImage,
  CaptchaInput,
  ErrorMessage,
} from './Captcha.styles';

const CaptchaComponent: React.FC<{
  register: any;
  error?: string;
  setHash: (hash: string) => any;
}> = ({ register, error, setHash }) => {
  const [captchaImage, setCaptchaImage] = useState('');
  const [captchaSolution, setCaptchaSolution] = useState('');
  const [errorMessage, setErrorMessage] = useState('');

  const loadCaptcha = async () => {
    try {
      const { data } = await api.get('captcha');
      setCaptchaImage(data.image);
      setCaptchaSolution(data.solution);
      setHash(data.hash);
    } catch (error) {
      setErrorMessage('Error loading CAPTCHA. Please try again.');
    }
  };

  useEffect(() => {
    loadCaptcha();
  }, []);

  const errorText = error || errorMessage || '\u00A0';

  return captchaImage ? (
    <>
      <CaptchaBody>
        <CaptchaImage
          src={captchaImage}
          alt='CAPTCHA'
          title={captchaSolution}
        />
        <CaptchaInput
          type='text'
          placeholder=''
          {...register}
          maxLength={6}
        />
      </CaptchaBody>
      {errorText && <ErrorMessage>{errorText}</ErrorMessage>}
    </>
  ) : (
    <></>
  );
};

export default CaptchaComponent;
