import React, { useEffect, useState } from 'react';
import {
  Header,
  CommentFormContainer,
  EditorContainer,
  SendButton,
} from './MessageForm.styled';
import { TNewComment, TNewCommentProp } from '../../store/Comment/types';
import { useAppDispatch, useAppSelector } from '../../hooks';
import { allCommentsAction } from '../../store/Comment/actions';
import TextEditor from '../TextEditor/TextEditor';
import { FieldError, SubmitHandler, useForm } from 'react-hook-form';
import InputField from '../InputField/InputField';
import { verifyHTML } from '../../services/verifyhtml';
import { addFiles } from '../../services';
import CaptchaComponent from '../Captcha/Captcha';

export const MessageForm: React.FC<{
  message_id: string;
  level?: number;
}> = ({ message_id, level = 0 }) => {
  const dispatch = useAppDispatch();
  const statusForm =
    useAppSelector((s) => s.commentState.statusForms[message_id]) ?? 'closed';
  const [hash, setHash] = useState('');

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
    setError,
  } = useForm<TNewCommentProp>();

  const onSubmit: SubmitHandler<TNewCommentProp> = async (data) => {
    dispatch(
      allCommentsAction.changeFormStatus({
        id: message_id,
        value: 'loading',
      })
    );
    const newData: TNewComment = {
      ...data,
      parent: message_id || null,
      file: undefined,
      captcha: {
        text: data.captcha,
        hash,
      },
    };

    if (data.file.length) {
      if (!data.file[0]?.link) {
        if (data.file[0].type === '' && data.file[0].size > 100 * 1024) {
          setError('file', {
            type: 'manual',
            message: 'File size should be less than 100kb',
          });
        } else if (
          !['image/png', 'image/jpeg', 'image/gif', 'plain/text'].includes(
            data.file[0].type
          )
        ) {
          setError('file', {
            type: 'manual',
            message: 'File type should be png, jpeg, gif or txt',
          });
        }
        const formdata = new FormData();
        formdata.append('file', data.file[0]);
        data.file[0].link = await addFiles(formdata);
      }
      newData.file = data.file[0].link;
      console.log(data.file);
    }

    dispatch(allCommentsAction.createCommentAction(newData));
  };

  useEffect(() => {
    if (statusForm === 'closed') {
      reset();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusForm]);

  const handleCancel = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(
      allCommentsAction.changeFormStatus({
        id: message_id,
        value: statusForm === 'closed' ? 'opened' : 'closed',
      })
    );
    reset();
  };
  return (
    <CommentFormContainer level={level + 1}>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Header>
          <InputField
            type='text'
            placeholder='Username'
            register={register('name', {
              required: 'Username is required',
            })}
            error={errors.name}
          />
          <InputField
            type='email'
            placeholder='Email'
            register={register('email', {
              required: 'Email is required',
              pattern: {
                value: /\S+@\S+\.\S+/,
                message: 'Entered value does not match email format',
              },
            })}
            error={errors.email}
          />
          <InputField
            type='url'
            placeholder='Homepage (optional)'
            register={register('homepage', {
              pattern: {
                value: /https?:\/\/.+/,
                message: 'Entered value does not match URL format',
              },
            })}
          />
        </Header>
        <EditorContainer>
          <TextEditor
            register={register('text', {
              required: 'Text is required',
              validate: (value: string) => {
                try {
                  verifyHTML(value);
                  return true;
                } catch (err: any) {
                  return err.message;
                }
              },
            })}
            error={errors.text}
          />
          <InputField
            type='file'
            register={register('file')}
            error={errors.file as FieldError}
            accept='image/png, image/jpeg, image/gif, plain/text'
          />
        </EditorContainer>
        <CaptchaComponent
          register={register('captcha', {
            required: '  ',
          })}
          error={
            errors.captcha?.message ||
            (['opened', 'closed'].includes(statusForm)
              ? undefined
              : ' ')
          }
          setHash={setHash}
        />

        <SendButton
          type='submit'
          loading={statusForm === 'loading'}
          disabled={statusForm === 'loading'}
        >
          
        </SendButton>
        <SendButton onClick={handleCancel}></SendButton>
      </form>
    </CommentFormContainer>
  );
};
