import { TextareaHTMLAttributes, useRef, useState } from 'react';
import { Toolbar, Button, Textarea, ErrorMessage } from './TextEditor.styled';
import { verifyHTML } from '../../services/verifyhtml';
import { FieldError } from 'react-hook-form';

const tagTemplates = {
  link: (text: string) => ({
    text: `<a href="" title="">${text}</a>`,
    length: 20,
  }),
  code: (text: string) => ({ text: `<code>${text}</code>`, length: 6 }),
  bold: (text: string) => ({ text: `<strong>${text}</strong>`, length: 8 }),
  italic: (text: string) => ({ text: `<i>${text}</i>`, length: 3 }),
};

const TextEditor: React.FC<
  {
    error?: FieldError | undefined;
    register: any;
  } & TextareaHTMLAttributes<HTMLTextAreaElement>
> = ({ error: err, register }) => {
  const [error, setError] = useState('');
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  const verify = (value: string) => {
    try {
      verifyHTML(value);
      setError('');
    } catch (err: any) {
      setError(err.message);
    }
  };

  const wrapSelectedText = (tag: string) => {
    const textarea = textareaRef.current;
    if (!textarea) return;

    const { selectionStart, selectionEnd } = textarea;
    const selectedText = textarea.value.substring(selectionStart, selectionEnd);

    const { text: wrappedText, length } =
      tagTemplates[tag as keyof typeof tagTemplates](selectedText);

    textarea.value =
      textarea.value.substring(0, selectionStart) +
      wrappedText +
      textarea.value.substring(selectionEnd);

    textarea.setSelectionRange(selectionStart + length, selectionEnd + length);

    textarea.focus();

    verify(textarea.value);
  };

  const hasError = error || err?.message || '\u00A0';

  return (
    <>
      <Toolbar>
        <Button onClick={() => wrapSelectedText('link')} type='button' tabIndex={-1}>
          
        </Button>
        <Button onClick={() => wrapSelectedText('code')} type='button' tabIndex={-1}>
          
        </Button>
        <Button onClick={() => wrapSelectedText('bold')} type='button' tabIndex={-1}>
          
        </Button>
        <Button onClick={() => wrapSelectedText('italic')} type='button' tabIndex={-1}>
          
        </Button>
      </Toolbar>
      <Textarea
        placeholder='Text'
        haserror={hasError !== '\u00A0'}
        {...register}
        ref={(e) => {
          register.ref(e);
          //@ts-ignore
          textareaRef.current = e;
        }}
        onChange={(e) => {
          verify(e.target.value);
          register.onChange(e);
        }}
      />
      {hasError && <ErrorMessage>{hasError}</ErrorMessage>}
    </>
  );
};

export default TextEditor;
