import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { TOneComment, TOneTopic } from './types';

type TStatusForm = 'opened' | 'closed' | 'error' | 'loading';

type TComment = {
  status: 'loading' | 'error' | '';
  commentRoot: TOneComment | null;
  countComments: number;
  commentsHashLinks: Record<string, string | null>; // id => parent
  statusForms: Record<string, TStatusForm>;
  topicSize: number;
  topic?: string;
  topicsList: TOneTopic[];
};
const initialState: TComment = {
  status: '',
  commentRoot: null,
  countComments: 0,
  commentsHashLinks: {},
  statusForms: {},
  topicSize: 0,
  topic: undefined,
  topicsList: [],
};
export const CommentsSlice = createSlice({
  name: 'CommentsState',
  initialState,
  reducers: {
    changeStatus: (state, { payload }: PayloadAction<TComment['status']>) => {
      state.status = payload;
    },
    changeFormStatus: (
      state,
      { payload }: PayloadAction<{ id: string; value: TStatusForm }>
    ) => {
      state.statusForms[payload.id] = payload.value;
    },
    addComments: (state, { payload }: PayloadAction<TOneComment>) => {
      const calculateCountComments = (comment: TOneComment): void => {
        state.commentsHashLinks[comment._id] = comment.parent;
        comment.replies?.forEach(calculateCountComments);
      };
      if (!state.commentRoot) {
        const { replies, ...root } = payload;
        state.commentRoot = root;
        state.countComments = 1;
        state.commentsHashLinks[root._id] = root.parent;
      }
      if (payload._id === state.topic) {
        state.topicSize = payload.commentsCount;
      }
      const mergeComments = (
        root: TOneComment,
        newComment: TOneComment
      ): void => {
        const map = new Map<string, TOneComment>();
        root.replies?.forEach((comment) => {
          map.set(comment._id, comment);
        });
        newComment.replies?.forEach((comment) => {
          if (map.has(comment._id)) {
            mergeComments(map.get(comment._id)!, comment);
            return;
          }
          map.set(comment._id, comment);
          calculateCountComments(comment);
        });
        root.replies = Array.from(map.values()).sort((a, b) => b.date - a.date);
      };
      mergeComments(state.commentRoot, payload);
      state.countComments = Object.keys(state.commentsHashLinks).length;
      state.status = '';
    },
    insertComment: (state, { payload }: PayloadAction<TOneComment>) => {
      const calculateCountComments = (comment: TOneComment): void => {
        state.commentsHashLinks[comment._id] = comment.parent;
        comment.replies?.forEach(calculateCountComments);
      };
      const findPath = (id: string): string[] => {
        if (!state.commentsHashLinks[id]) return [];
        return [...findPath(state.commentsHashLinks[id] || ''), id];
      };
      const insert = (
        root: TOneComment,
        path: string[],
        comment: TOneComment
      ): void => {
        if (path.length === 0) {
          root.replies?.push(comment);
          root.replies = root.replies?.sort((a, b) => b.date - a.date);
          calculateCountComments(comment);
          return;
        }
        const id = path.shift()!;
        const commentIndex = root.replies!.findIndex((c) => c._id === id);
        insert(root.replies![commentIndex], path, comment);
      };
      insert(state.commentRoot!, findPath(payload.parent!), payload);
      state.countComments = Object.keys(state.commentsHashLinks).length;
    },
    setTopics: (state, { payload }: PayloadAction<TOneTopic[]>) => {
      const map = new Map<string, TOneTopic>();

      payload.forEach((topic) => {
        map.set(topic._id, topic);
      });

      state.topicsList.forEach((topic) => {
        map.set(topic._id, topic);
      });

      state.topicsList = Array.from(map.values());
    },
    changeTopic(state, { payload }: PayloadAction<string>) {
      if (state.topic === payload) return;

      state.topic = payload;
      state.commentRoot = null;
      state.countComments = 0;
      state.commentsHashLinks = {};
    },
  },
});
export const CommentsActions = CommentsSlice.actions;
export const CommentsReducers = CommentsSlice.reducer;
