import { call, put, takeLatest } from 'redux-saga/effects';
import { TOneComment, TOneTopic } from './types';
import { allCommentsAction } from './actions';
import { getCommentsApi, createCommentsApi, getTopicsApi } from './api';
import { t } from 'i18next';
import { toast } from 'react-toastify';
import { store } from '../store';
const { getCommentsAction, getTopicsAction, createCommentAction } =
  allCommentsAction;

function* getCommentsWorker({ payload }: ReturnType<typeof getCommentsAction>) {
  const { countComments } = store.getState().commentState;
  yield put(allCommentsAction.changeStatus('loading'));

  try {
    const response: TOneComment = yield call(getCommentsApi, {
      topic: payload,
      commentsCount: countComments,
    });
    yield put(allCommentsAction.addComments(response));
  } catch (error) {
    yield put(allCommentsAction.changeStatus('error'));
  }
}

function* getTopicsWorker() {
  yield put(allCommentsAction.changeStatus('loading'));

  try {
    const response: TOneTopic[] = yield call(getTopicsApi);
    yield put(allCommentsAction.setTopics(response));
  } catch (error) {
    yield put(allCommentsAction.changeStatus('error'));
  }
}

function* createCommentWorker({
  payload,
}: ReturnType<typeof createCommentAction>) {
  const nameOfPage = t('toasts.nameOfPage.comment');

  try {
    yield call(createCommentsApi, payload);
    toast.success(t('toasts.createdSuccess', { nameOfPage }));
    yield put(
      allCommentsAction.changeFormStatus({
        id: payload.parent || '',
        value: 'closed',
      })
    );
  } catch (error: any) {
    yield put(
      allCommentsAction.changeFormStatus({
        id: payload.parent || '',
        value: error.message,
      })
    );
    toast.error(t('toasts.error'));
  }
}

export function* CommentsWatcher() {
  yield takeLatest(getCommentsAction, getCommentsWorker);
  yield takeLatest(getTopicsAction, getTopicsWorker);
  yield takeLatest(createCommentAction, createCommentWorker);
}
