import React, { useEffect } from 'react';
import { GlobalStyle } from './utils/globalStyle';
import { useAppDispatch, useAppSelector } from './hooks';
import { allCommentsAction } from './store/Comment/actions';
import { socket } from './socket';
import { MessageComponent } from './components/Message';
import { TopicTable } from './components/TopicTable/TopicTable';
import { BackPane, Button, PageContainer } from './App.styled';
import { MessageForm } from './components/MessageForm/MessageForm';
import { Modal } from './components/Modal/Modal';
import { TOneComment, TOneTopic } from './store/Comment/types';

const App: React.FC = () => {
  const { commentRoot, topic, topicSize, countComments, statusForms } =
    useAppSelector((state) => state.commentState);
  const statusForm = statusForms[''] ?? 'closed';

  const dispatch = useAppDispatch();

  useEffect(() => {
    socket.on('newComment', (data: TOneComment) => {
      dispatch(allCommentsAction.insertComment(data));
    });

    socket.on('newTopic', (data: TOneTopic) => {
      dispatch(allCommentsAction.setTopics([data]));
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    socket.emit('topic', topic);
    loadMoreMessages();

    const onReconnect = () => {
      socket.emit('topic', topic);
    };
    socket.io.on('reconnect', onReconnect);

    return () => {
      socket.io.off('reconnect', onReconnect);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [topic]);

  const loadMoreMessages = () => {
    if (topic) {
      dispatch(allCommentsAction.getCommentsAction(topic));
    }
  };

  const openNewTopic = () => {
    dispatch(
      allCommentsAction.changeFormStatus({
        id: '',
        value: 'opened',
      })
    );
  };

  const closeNewTopic = () => {
    dispatch(
      allCommentsAction.changeFormStatus({
        id: '',
        value: 'closed',
      })
    );
  };

  const closeTopic = () => {
    dispatch(allCommentsAction.changeTopic(''));
  };

  return (
    <PageContainer>
      <GlobalStyle />
      {commentRoot && (
        <BackPane onClick={closeTopic}>
          <img src='images/back.png' />
          <span></span>
        </BackPane>
      )}
      {!commentRoot && <TopicTable />}
      {commentRoot && <MessageComponent message={commentRoot} />}
      {commentRoot && countComments <= topicSize && (
        <Button onClick={loadMoreMessages}>Load More</Button>
      )}
      {!commentRoot && (
        <>
          <Modal isOpen={statusForm !== 'closed'} onClose={closeNewTopic}>
            <MessageForm message_id='' level={-1} />
          </Modal>
          <Button onClick={openNewTopic}>  </Button>
        </>
      )}
    </PageContainer>
  );
};

export default App;
